<template>
  <div v-if="loaded">
    <div class="rotation">
      <el-carousel trigger="hover" height="400px">
        <el-carousel-item v-for="item in rotationImgs" :key="item">
          <img class="small" :src="item" alt="" />
        </el-carousel-item>
      </el-carousel>
    </div>
    <div class="tabnav">
      <div
        class="tab-nav-item tab-hot font-bold"
        :class="{ hotbg: tabhotflag }"
        @click="checkhot"
      >
        正在热映
      </div>
      <div
        class="tab-nav-item tab-up font-bold"
        :class="{ hotbg: !tabhotflag }"
        @click="checkup"
      >
        即将上映
      </div>
    </div>
    <div class="nut-radiogroup">
      <label class="inline" for="byhot">
        <span>
          <input
            type="radio"
            name="checkbytype"
            id="byhot"
            value="byhot"
            style="height: 14px; width: 14px"
            v-model="queryForm.selectType"
            @click="fetchData"
          />
          <!--          <label for="radio" class="radio-beauty"></label>-->
        </span>
        热门推荐
      </label>
      <label class="inline" for="bytime">
        <span>
          <input
            type="radio"
            name="checkbytype"
            id="bytime"
            value="bytime"
            style="height: 14px; width: 14px"
            v-model="queryForm.selectType"
            @click="fetchData"
          />
          <!--          <label for="radio" class="radio-beauty"></label>-->
        </span>
        按时间排序
      </label>
      <label class="inline" v-if="tabhotflag" for="byscore">
        <span>
          <input
            type="radio"
            name="checkbytype"
            id="byscore"
            value="byscore"
            style="height: 14px; width: 14px"
            v-model="queryForm.selectType"
            @click="fetchData"
          />
          <!--          <label for="byscore" class="radio-beauty"></label>-->
        </span>
        按评分排序
      </label>
      <label class="inline" v-else-if="tabupflag" for="bywantnum">
        <span>
          <input
            type="radio"
            name="checkbytype"
            id="bywantnum"
            value="bywantnum"
            style="height: 14px; width: 14px"
            v-model="queryForm.selectType"
            @click="fetchData"
          />
          <!--          <label for="radio" class="radio-beauty"></label>-->
        </span>
        按想看人数排序
      </label>
    </div>
    <div class="filmshow">
      <div v-if="listLoading" class="listLoading">
        <img
          src="@/assets/index_img/Loading.png"
          class="loading-img"
        /><br /><br />
        <span class="loading-span">{{ elementLoadingText }}</span>
      </div>
      <ul>
        <li v-for="(item, index) in list" :key="index">
          <film-show
            :movieId="item.movieId"
            :movieName="item.movieName"
            :movieScore="item.movieScore"
            :wantsNum="item.wantsNum"
            :lowestPrice="item.lowestPrice"
            :moviePicture="item.moviePicture"
            :movieLoaded="loaded"
          ></film-show>
        </li>
      </ul>
    </div>
    <el-pagination
      class="film-page"
      background
      :current-page="queryForm.pageNo"
      :page-size="queryForm.pageSize"
      :layout="layout"
      :total="total"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
    ></el-pagination>
  </div>
</template>
<script>
import filmShow from "./filmShow";
import { indexList } from "@/api/FilmList";

export default {
  name: "Index",
  data() {
    return {
      loaded: false,
      rotationImgs: [
        "https://img14.360buyimg.com/itcomm/jfs/t1/146985/21/9644/178592/5f745e8fece315ab1/c5cf861c0d367493.jpg",
        "https://img14.360buyimg.com/itcomm/jfs/t1/140494/15/9683/197269/5f744b82e8e0d8275/e3f36a76fe496d04.jpg",
        "https://liangcang-material.alicdn.com/prod/upload/1ba76953241c4ee284985755da7c987d.jpg?x-oss-process=image/resize,w_2074/interlace,1/quality,Q_80/sharpen,100",
        "https://liangcang-material.alicdn.com/prod/upload/04d003edc37f4c6caf2e5291c6a2f0aa.jpg?x-oss-process=image/resize,w_2074/interlace,1/quality,Q_80/sharpen,100",
        "https://liangcang-material.alicdn.com/prod/upload/807a85bb8c474adeab92f73527826c75.jpg?x-oss-process=image/resize,w_2074/interlace,1/quality,Q_80/sharpen,100",
      ],
      tabhotflag: true,
      tabupflag: false,
      list: null,
      listLoading: true,
      layout: "total, sizes, prev, pager, next, jumper",
      total: 0,
      selectRows: "",
      elementLoadingText: "正在加载......",
      queryForm: {
        pageNo: 1,
        pageSize: 20,
        timeType: "zzry",
        selectType: "byhot",
      },
    };
  },
  created() {
    this.fetchData();
  },
  mounted() {},
  methods: {
    checkhot() {
      this.tabhotflag = true;
      this.tabupflag = false;
      this.queryForm.timeType = "zzry";
      this.fetchData();
    },
    checkup() {
      this.tabhotflag = false;
      this.tabupflag = true;
      this.queryForm.timeType = "jjsy";
      this.fetchData();
    },
    handleSizeChange(val) {
      this.queryForm.pageSize = val;
      this.fetchData();
    },
    handleCurrentChange(val) {
      this.queryForm.pageNo = val;
      this.fetchData();
    },
    queryData() {
      this.queryForm.pageNo = 1;
      this.fetchData();
    },
    async fetchData() {
      this.list = null;
      this.listLoading = true;
      const { data, totalCount } = await indexList(this.queryForm);
      this.loaded = true;
      this.list = data;
      this.total = totalCount;
      this.listLoading = false;
    },
  },
  components: {
    "film-show": filmShow,
  },
};
</script>
<style>
ul {
  list-style-type: none;
}

.el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 150px;
  margin: 0;
}

.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n + 1) {
  background-color: #d3dce6;
}

.rotation {
  margin-top: 30px;
}

.small {
  width: 100%;
  height: 100%;
}

.tabnav {
  width: 100%;
  height: 80px;
  line-height: 80px;
  text-align: center;
  background-image: url("http://misc.360buyimg.com/virtuals/movie/1.0.0/img/tab.png");
}

.tab-nav-item {
  display: inline-block;
  width: 270px;
  position: relative;
  text-align: center;
  font-size: 20px;
  font-weight: 700;
  color: hsla(0, 0%, 100%, 0.45);
  cursor: pointer;
}

.tab-nav-item.tab-hot:before {
  content: " ";
  position: absolute;
  width: 24px;
  height: 24px;
  top: 27px;
  left: 60px;
  background: url(data:image/png;base64,)
    no-repeat;
}
.tab-nav-item.tab-hot.hotbg:before {
  background: url(data:image/png;base64,)
    no-repeat;
}

.tab-nav-item.tab-up:before {
  content: " ";
  position: absolute;
  width: 24px;
  height: 24px;
  top: 27px;
  left: 60px;
  background: url(data:image/png;base64,)
    no-repeat;
}
.tab-nav-item.tab-up.hotbg:before {
  background: url(data:image/png;base64,)
    no-repeat;
}

.hotbg {
  color: #fff;
}

.hotbg:after {
  content: " ";
  position: absolute;
  width: 80px;
  height: 15px;
  bottom: -5px;
  left: 95px;
  background: url(data:image/png;base64,)
    no-repeat;
}

.font-bold {
  font-family: JDZHENGHEI-01-BOLD;
}

.nut-radiogroup {
  margin-right: 40px;
  margin-top: 38px;
  margin-bottom: 24px;
  font-size: 12px;
  color: #252525;
}

.inline {
  margin-right: 40px;
  font-size: 15px;
}

.inline input[type="radio"] {
  vertical-align: middle;
}

.filmshow {
  width: 100%;
  min-height: 700px;
  margin: 0 auto;
  margin-top: 30px;
  margin-left: 50px;
}

.filmshow ul {
  font-size: 0;
  /* text-align: center; */
}

.filmshow ul li {
  display: inline-block;
  width: 18%;
  margin-right: 15px;
  text-align: center;
}

.filmshow ul li a {
  display: inline-block;
  width: 100%;
}

.el-pagination .is-background {
  margin-bottom: 10px;
}

.listLoading {
  width: 100%;
  height: 100%;
  /*background-color: #F6F8F9;*/
  text-align: center;
  margin: auto;
  position: relative;
  z-index: 1;
}

.loading-img {
  margin-top: 20%;
  border: 0;
  vertical-align: bottom;
  width: 82px;
  height: 82px;
  -webkit-animation: loading-rotate 3s linear infinite;
  animation: loading-rotate 3s linear infinite;
}

.loading-span {
  font-size: 20px;
  color: #ff5c5c;
  font-weight: bold;
}

@keyframes loading-rotate {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(359deg);
    transform: rotate(359deg);
  }
}

.film-page {
  text-align: center;
}

.film-page .el-pager li:not(.disabled).active {
  background-color: #f80f01 !important;
}
</style>
