<template>
  <div class="orderGenerated-container" v-if="loaded">
    <div class="count-down-wrapper">
      <div class="count-down">
        <p class="time-left">
          请在
          <span class="minute">{{ minutes }}</span>
          分钟
          <span class="second">{{ seconds }}</span
          >秒内完成支付
        </p>
        <p class="tip">
          超时订单会自动取消，如对订单问题，请点击在线客服或致电毅购：18103549919
        </p>
      </div>
    </div>
    <table class="order-table">
      <thead>
        <tr>
          <th>影片</th>
          <th>时间</th>
          <th>座位</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td class="movie-name">《{{ orderInfo.movieName }}》</td>
          <td class="showtime">{{ startTime }}</td>
          <td>
            <span class="hall">{{ orderInfo.arrangementPlace }}号厅</span>
            <div class="seats">
              <div>
                <span
                  class=""
                  v-for="(item, index) in orderInfo.orderSeat"
                  :key="index"
                  ><i>{{ item }}</i></span
                >
              </div>
              <div></div>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
    <div class="right">
      <div class="price-wrapper">
        <span>实际支付 :</span>
        <span class="price">{{ orderInfo.orderPrice }}</span>
      </div>
      <div>
        <div class="cancle-btn" @click="canclePay">取消订单</div>
        <div class="pay-btn" @click="confirmPay">确认支付</div>
      </div>
    </div>
    <div class="modal-container" style="" v-if="overTimeFlag">
      <div class="modal">
        <span class="icon"></span>

        <p class="tip">支付超时，该订单已失效，请重新购买</p>

        <div class="ok-btn btn" @click="reSelectSeat">我知道了</div>
      </div>
    </div>
  </div>
</template>
<script>
import { getOrderInfo, jumpToPay, delOrder } from "@/api/order";
import { formatTime } from "@/utils/index";
import path from "path";
import { log } from "util";
import { setTimeout } from "timers";
export default {
  name: "OrderGenerated",
  data() {
    return {
      loaded: false,
      orderId: this.$route.query.orderId,
      orderInfo: Object,
      minutes: 15,
      seconds: 0,
      timer: "",
      overTimeFlag: false,
      startTime: "",
    };
  },
  created() {
    this.fetchData();
  },

  beforeDestroy() {
    // clearInterval(this.timer);
  },
  watch: {
    // 利用watch方法检测路由变化：
    $route: "fetchData",
  },
  computed: {},
  methods: {
    async fetchData() {
      const data = await getOrderInfo({
        orderId: this.orderId,
      });
      if (data == null || data == "") {
        this.$router.replace({
          path: "/404",
        });
      }
      this.orderInfo = data;
      let orderDate = new Date(this.orderInfo.orderTime);
      orderDate.setMinutes(orderDate.getMinutes() + 15);
      let nowDate = new Date();
      let difftime = (orderDate - nowDate) / 1000; //计算时间差,并把毫秒转换成秒
      console.log(difftime);
      this.minutes = parseInt((difftime % 3600) / 60); // 分钟 -(day*24) 以60秒为一整份 取余 剩下秒数 秒数/60 就是分钟数
      this.seconds = parseInt(difftime % 60); // 以60秒为一整份 取余 剩下秒数
      let arrangmentDate = new Date(this.orderInfo.arrangementTime);
      const weekdays = ["周日", "周一", "周二", "周三", "周四", "周五", "周六"];
      let weekday = "";
      if (
        nowDate.getFullYear() == orderDate.getFullYear() &&
        nowDate.getMonth() == orderDate.getMonth() &&
        nowDate.getDate() == orderDate.getDate()
      ) {
        weekday = "今天";
      } else {
        weekday = weekdays[arrangmentDate.getDay()];
      }
      this.startTime =
        arrangmentDate.getMonth() +
        1 +
        "月" +
        arrangmentDate.getDate() +
        "日 " +
        weekday +
        " " +
        arrangmentDate.getHours() +
        ":" +
        arrangmentDate.getMinutes();
      this.createTimer();
      log("获取数据");
      this.loaded = true;
    },

    createTimer() {
      if (this.timer != null && this.timer != "") {
        clearInterval(this.timer);
      }
      log(this.timer);
      if (this.timer == 0 || this.timer == "") {
        this.timer = setInterval(this.renewSeconds, 1000);
      }
    },

    renewSeconds() {
      if (this.seconds > 0) {
        this.seconds--;
      } else if (this.seconds == 0) {
        if (this.minutes > 0) {
          this.minutes--;
          this.seconds = 59;
        } else {
          this.overTimeFlag = true;
          clearInterval(this.timer);
          this.timer = 0;
          this.minutes = 15;
          this.seconds = 0;
        }
      }
    },
    reSelectSeat() {
      this.overTimeFlag = false;
      this.$router.push({
        path: "/movies/movieDetail?movieName=" + this.orderInfo.movieName,
      });
    },

    async canclePay() {
      this.$confirm("此操作将取消该订单, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          delOrder(this.orderInfo.orderId).then((res) => {
            console.log(res);
            if (res === "success") {
              this.$router.push({
                path: "/ticket/seatSelection",
                query: {
                  arrangementId: this.orderInfo.arrangementId,
                },
              });
            } else {
              this.$message({
                message: "取消订单失败,请重新尝试!",
                type: "error",
              });
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },

    async confirmPay() {
      let message = jumpToPay(this.orderInfo.orderId).then((res) => {
        // if (res.status === 200 && this.form.type == 2) {
        // if (res.status === 200) {
        //返回参数
        console.log(res);
        let routerData = this.$router.resolve({
          path: "/ticket/finance/applyText",
          query: { htmls: res },
        });
        this.htmls = res;
        //打开新页面
        window.location.href = routerData.href;
        // window.open(routerData.href);
        const div = document.createElement("div");
        div.innerHTML = htmls;
        document.body.appendChild(div);
        document.forms[0].submit();
        // }
      });
      console.log(message);
      // console.log(result);
      // this.$router.replace({
      //   path: "/ticket/orderFinished",
      //   query: {
      //     orderId: this.orderInfo.orderId,
      //   },
      // });
    },
  },
};
</script>

<style>
.count-down-wrapper {
  width: 100%;
  height: 90px;
  padding-top: 20px;
  margin-bottom: 40px;
  margin-top: 70px;
  background-color: #fff3f3;
}
.count-down-wrapper .count-down {
  padding-left: 55px;
  min-height: 41px;
  margin-left: 30px;
  background: url(data:image/png;base64,)
    no-repeat;
  background-position: 0 17px;
}
.count-down-wrapper .time-left {
  font-size: 16px;
  color: #666;
  margin-bottom: 4px;
}
.count-down-wrapper .time-left .minute,
.count-down-wrapper .time-left .second {
  font-size: 32px;
  color: #f03d37;
  margin: 0 5px;
}
.count-down-wrapper .tip {
  font-size: 14px;
  color: #f03d37;
}
.order-table {
  width: 100%;
  border: 1px solid #e5e5e5;
  border-spacing: 0;
  margin-bottom: 60px;
}
.order-table thead {
  background-color: #f7f7f7;
}
tr {
  display: table-row;
  vertical-align: inherit;
  border-color: inherit;
}
.order-table thead th {
  font-size: 16px;
  color: #333;
  width: 25%;
  padding: 14px 0;
  font-weight: 400;
}
tbody {
  display: table-row-group;
  vertical-align: middle;
  border-color: inherit;
}
.order-table td {
  text-align: center;
  padding: 20px 0;
  color: #333;
}
.order-table .movie-name {
  font-size: 16px;
}
.order-table .showtime {
  font-size: 14px;
  color: #f03d37;
}
.order-table .hall {
  display: inline-block;
  font-size: 14px;
  color: #424242;
  font-weight: 700;
  margin-right: 10px;
  vertical-align: top;
}
.order-table .seats {
  display: inline-block;
  text-align: left;
}
.order-table .seats span {
  font-size: 12px;
  margin-right: 2px;
}
.order-table .seats i {
  font-size: 16px;
  font-style: normal;
  margin: 0 2px;
}
.right {
  text-align: right;
}
.right .price-wrapper {
  font-size: 14px;
  color: #333;
  margin-bottom: 17px;
}
.right .price-wrapper .price {
  font-size: 36px;
  color: #f03d37;
}
.right .price-wrapper .price:before {
  content: "\FFE5";
  font-size: 24px;
}
.btn,
.pay-btn {
  cursor: pointer;
  display: inline-block;
  width: 190px;
  height: 42px;
  line-height: 42px;
  text-align: center;
  color: #fff;
  background-color: #f03d37;
  border-radius: 100px;
  -webkit-box-shadow: 0 2px 10px -2px #f03d37;
  box-shadow: 0 2px 10px -2px #f03d37;
}

.cancle-btn {
  cursor: pointer;
  display: inline-block;
  width: 190px;
  height: 42px;
  line-height: 42px;
  text-align: center;
  margin-right: 20px;
  color: #fff;
  background-color: #b5b5b5;
  border-radius: 100px;
  -webkit-box-shadow: 0 2px 10px -2px #b5b5b5;
  box-shadow: 0 2px 10px -2px #b5b5b5;
}

.modal-container {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.4);
}
.modal-container .modal {
  width: 460px;
  height: 155px;
  padding: 30px 0;
  position: absolute;
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #fff;
  text-align: center;
}
.modal-container .icon {
  display: inline-block;
  width: 50px;
  height: 50px;
  background: url(data:image/png;base64,)
    no-repeat;
}
.modal-container .tip {
  font-size: 14px;
  color: #666;
  margin-bottom: 26px;
  margin-top: 12px;
}
.modal-container .ok-btn {
  color: #fff;
  background-color: #f03d37;
  -webkit-box-shadow: 0 2px 10px -2px #f03d37;
  box-shadow: 0 2px 10px -2px #f03d37;
}
</style>