  left: 50%;
}

.seats-block .seats-wrapper {
  display: inline-block;
  min-width: 550px;
  margin-left: 30px;
}
.seats-block .row {
  white-space: nowrap;
  margin-bottom: 10px;
  text-align: center;
}

.seats-block .seat {
  display: inline-block;
  font-size: 0;
  width: 30px;
  height: 26px;
  margin: 0 5px;
  background: url(data:image/png;base64,)
    no-repeat;
  background-position: 0 1px;
}
.seats-block .seat.sold {
  background-image: url(data:image/png;base64,);
}
.seats-block .seat.selected {
  background-image: url(data:image/png;base64,);
}

.seat-selection .main .movie-show {
  width: 340px;
  background-color: #f9f9f9;
  padding: 20px;
  display: inline-block;
}

.movie-show .movie-info .poster {
  width: 115px;
  height: 158px;
  border: 2px solid #fff;
  -webkit-box-shadow: 0 2px 7px 0 hsla(0, 0%, 53%, 0.5);
  box-shadow: 0 2px 7px 0 hsla(0, 0%, 53%, 0.5);
  float: left;
