.movie-show .movie-info .content {
  margin-left: 140px;
}
.movie-show .movie-info .content .name {
  font-size: 20px;
  font-weight: 700;
  color: #333;
  margin: 0 0 14px;
}
.movie-show .info-item {
  font-size: 12px;
  color: #999;
  margin-bottom: 4px;
}
.movie-show .info-item .value {
  color: #151515;
  margin-left: 2px;
}

.movie-show .show-info {
  margin-top: 110px;
}

.movie-show .show-info .info-item {
  margin-bottom: 9px;
  clear: both;
}

.movie-show .show-info .info-item > span {
  display: inline-block;
  vertical-align: top;
  font-size: 14px;
}

.movie-show .show-info .info-item .value {
  width: 85%;
}

.movie-show .ticket-info {
  padding: 20px 0 10px;
  border-top: 1px dashed #e5e5e5;
  border-bottom: 1px dashed #e5e5e5;
}

.movie-show .ticket-info .buy-limit {
  font-size: 14px;
  color: #999;
  margin: 0;
}

.movie-show .ticket-info .no-selected {
  font-size: 14px;
  color: #333;
  text-align: center;
  margin: 28px 0 39px;
}

.movie-show .ticket-info .no-selected span {
  color: #f03d37;
}

.movie-show .ticket-info .text {
  font-size: 14px;
  color: #999;
  float: left;
}
.movie-show .ticket-info .ticket-container {
  margin-left: 42px;
  margin-bottom: 20px;
  position: relative;
  top: -5px;
}

.movie-show .ticket-info .ticket {
  cursor: default;
  position: relative;
  font-size: 12px;
  color: #f03d37;
  display: inline-block;
  width: 60px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  margin: 0 12px 10px 0;
  background: url(data:image/png;base64,)
    no-repeat;
}

.movie-show .ticket-info .total-price {
  font-size: 14px;
  color: #333;
}

.movie-show .ticket-info .total-price .price {
  color: #f03d37;
  font-size: 24px;
}

.movie-show .ticket-info .total-price .price:before {
  content: "\FFE5";
  font-size: 14px;
}

.confirm-order {
  text-align: center;
  margin-top: 20px;
}

.confirm-order .iLoginComp-login-btn-wrapper {
  -webkit-font-smoothing: subpixel-antialiased;
  visibility: visible;
  font: inherit;
  margin: 0;
  overflow: visible;
  text-transform: none;
  text-align: center;
  background: #e5e5e5;
  height: 2.8em;
  border: none;
  outline: none;
  cursor: pointer;
  user-select: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  width: 260px;
  line-height: 42px;
  border-radius: 21px;
  font-size: 16px;
  color: #fff;
  box-shadow: 0 2px 10px -2px #f03d37;
  background-color: #f03d37;
}

.movie-show .confirm-order .iLoginComp-login-btn-wrapper.disable {
  cursor: default;
  background-color: #dedede;
  -webkit-box-shadow: none;
  box-shadow: none;
}
</style>