  font-family: Microsoft YaHei, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: subpixel-antialiased;
  text-decoration: none;
  outline-width: 0;
  cursor: pointer;
  float: left;
  display: block;
  width: 120px;
  height: 25px;
  background-color: #756189;
  margin-right: 10px;
  padding-top: 11px;
  text-align: center;
  font-size: 14px;
  line-height: 16px;
  color: #fff;
  border-radius: 2px;
}

/* .movieScore-btn-icon.scored {
  background-image: url(data:image/png;base64,);
} */

.movieScore-btn-msg {
  font-family: Microsoft YaHei, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: subpixel-antialiased;
  cursor: pointer;
  text-align: center;
  font-size: 14px;
  line-height: 16px;
  color: #fff;
}

.movieScore-btn-msg.movieScore-num {
  color: #ffc605;
}

.btn.buy {
  font-family: Microsoft YaHei, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: subpixel-antialiased;
  text-decoration: none;
  display: inline-block;
  color: #fff;
  background-color: #df2d2d;
  margin-top: 10px;
  width: 250px;
  height: 40px;
  font-size: 16px;
  line-height: 40px;
  text-align: center;
  border-radius: 2px;
  padding: 0;
  cursor: pointer;
}

.movie-movieScore-all {
  width: 300px;
  position: relative;
  bottom: 100px;
  left: 650px;
}

.movieScore-all-part-scorenum {
  font-size: 30px;
  background-color: #fed603;
  display: block;
  width: 65px;
  height: 45px;
  line-height: 45px;
  text-align: center;
  font-weight: bolder;
  float: left;
  border-radius: 0 6px 6px 0;
}

/* 星星大小 */
.movie-movieScore-all-part .el-icon-star-on:before {
  font-size: 35px;
}

.movieDetail-middle {
  margin-top: 30px;
  width: 720px;
  margin-left: 50px;
  float: left;
}

.movieDetail-middle a {
  color: black;
  margin-right: 20px;
}

.movieDetail h3 {
  font-weight: normal;
  font-size: 18px;
}

.movieDetail h2 {
  font-family: Microsoft YaHei, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: subpixel-antialiased;
  display: inline-block;
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 18px;
  color: #333;
  line-height: 65px;
}

.movieDetail h2:before {
  content: "";
  display: inline-block;
  width: 4px;
  height: 22px;
  margin-right: 6px;
  background-color: #ef4238;
  vertical-align: middle;
}

.movieDetail-middle span {
  font-family: Microsoft YaHei, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: subpixel-antialiased;
  color: #333;
  font-size: 16px;
  line-height: 26px;
}

.evaluation-list {
  width: 730px;
}
.evaluation-list ul {
  list-style: none;
}
.evaluation-list ul li {
  margin-top: 30px;
}

.evaluation-reloadmore {
  text-align: center;
  margin-top: 25px;
}

.evaluation-reloadmore .el-pager li:not(.disabled).active {
  background-color: #f80f01 !important;
}

.evaluation-list .el-loading-mask .el-loading-spinner .path {
  stroke: red;
}

.movieDetail-middle-right {
  width: 400px;
  float: right;
  margin-top: 30px;
  margin-right: 50px;
}

.relativeMovies dd {
  text-align: center;
  color: black;
  line-height: 28px;
}
.relativeMovies dd a {
  color: black;
  line-height: 26px;
}
</style>